import React, { Component, Suspense, memo } from 'react';
import { toast } from "react-toastify";
import { Accordion, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import PerfectScrollbar from 'react-perfect-scrollbar'
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import ModernWaterfallTemplate from './Components/ModernWaterfallTemplate';
import ModernSlideshowTemplate from './Components/ModernSlideshowTemplate';
import AtmosphereTemplate from './Components/AtmosphereTemplate';

import SquarePhoto from '../ThemeTemplates/SquarePhoto';
import ModernCard from '../ThemeTemplates/ModernCard';
import HorizontalColumns from '../ThemeTemplates/HorizontalColumns';
import ModernSlideShow from '../ThemeTemplates/ModernSlideShow';
import Atmosphere from '../ThemeTemplates/Atmosphere';
import GalleryPhoto from '../ThemeTemplates/GalleryPhoto';
import ModernWaterfall from '../ThemeTemplates/ModernWaterfall';
import FontHead from './FontHead';

import { getThemesList, createTemplate } from '../../../../Services/Actions/templateAction'

const ModernCardTemplate = React.lazy(() => import('./Components/ModernCardTemplate'));
const SquarePhotoTemplate = React.lazy(() => import('./Components/SquarePhotoTemplate'));
const HorizontalColumnTemplate = React.lazy(() => import('./Components/HorizontalColumnTemplate'));
const GalleryPhotoTemplate = React.lazy(() => import('./Components/GalleryPhotoTemplate'));
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));

const ThemesPreview = ({ themeName, templateFormData }) => {
    switch (themeName) {
        case 20:
            return <ModernCard themeId={themeName} {...templateFormData} />;
        case 4:
            return <SquarePhoto themeId={themeName} {...templateFormData} />;
        case 47:
            return <HorizontalColumns themeId={themeName} {...templateFormData} />;
        case 21:
            return <ModernSlideShow themeId={themeName} {...templateFormData} />;
        case 44:
            return <Atmosphere themeId={themeName} {...templateFormData} />;
        case 50:
            return <GalleryPhoto themeId={themeName} {...templateFormData} />;
        case 58:
            return <ModernWaterfall themeId={themeName} {...templateFormData} />;
        default:
            return <ModernCard themeId={themeName} {...templateFormData} />;
    }

}


class TemplateAside extends Component {
    constructor(props) {
        super(props)
        this.state = {
            templateName: props.defaultTheme && props.defaultTheme.themeDefaultOption ? props.defaultTheme.themeDefaultOption.name : "",
            selectedTheme: props.defaultTheme ? { name: props.defaultTheme.name, themeId: props.defaultTheme.themeId } : {},
            selectedThemeData: props.defaultTheme ? props.defaultTheme : null,
            templateFormData: null,
            isRequesting: false,
            tempError: false
        }
    }

    onChooseTheme = (theme, selectedThemeData) => event => {
        this.setState({ selectedTheme: theme, selectedThemeData: selectedThemeData })


    }
    onTextTemplateName = event => {
        this.setState({ templateName: event.target.value, tempError: false })
    }
    tempFormData = (data) => {
        this.setState({ templateFormData: data })
    }
    onClickSaveChanges = event => {
        const { templateFormData, templateName, selectedThemeData } = this.state
        const { templateId, onSiteSetting } = this.props
        if (templateFormData && Object.keys(templateFormData).length > 0 && String(templateName).length > 0) {
            var self = this

            const onsite_settings = {
                network: onSiteSetting.network,
                filter: onSiteSetting.filter,
                moderation: onSiteSetting.moderation,
                onsite_qr_code_btn_text: onSiteSetting.onsite_qr_code_btn_text,
                onsite_btn_color: templateFormData.onSiteUpload.bgColor,
                onsite_btn_txt_color: templateFormData.onSiteUpload.textColor,
                onsite_link_font_btn: onSiteSetting.onsite_link_font_btn,
                onsite_css_font_btn: onSiteSetting.onsite_css_font_btn,
                onsite_font_varient_btn: onSiteSetting.onsite_font_varient_btn,
                btn_fontSize: templateFormData.onSiteUpload.fontSize,
                onsite_qr_code_status: templateFormData.onSiteUpload.qrCodeStatus,
                onsite_popup_title: onSiteSetting.onsite_popup_title,
                onsite_popup_btn_txt: templateFormData.onSiteUpload.buttonText,
                onsite_popup_sub_heading: onSiteSetting.onsite_popup_sub_heading,
                onsite_term_url: onSiteSetting.onsite_term_url,
                onsiteImgBack: onSiteSetting.onsiteImgBack,
                onsite_logo: onSiteSetting.onsite_logo,
                Onsite_logoImageValue: onSiteSetting.Onsite_logoImageValue,
                onsite_bg_color_wg: onSiteSetting.onsite_bg_color_wg,
                onsiteBackImage: onSiteSetting.onsiteBackImage,
                onsite_bg_image_wg: onSiteSetting.onsite_bg_image_wg,
                Onsite_backImageValue: onSiteSetting.Onsite_backImageValue,
                onsite_succ_msg: onSiteSetting.onsite_succ_msg,
                onsite_thank_txt_color: onSiteSetting.onsite_thank_txt_color,
                onsite_link_font_msg: onSiteSetting.onsite_link_font_msg,
                onsite_css_font_msg: onSiteSetting.onsite_css_font_msg,
                onsite_font_varient_msg: onSiteSetting.onsite_font_varient_msg,
                onsite_thank_fontSize: onSiteSetting.onsite_thank_fontSize,
                onsite_status: templateFormData.onSiteUpload.buttonStatus,
                btn_radius: templateFormData.onSiteUpload.borderRadius
            };
            const data = {
                "action": 2,
                "id": templateId,
                "name": templateName,
                "theme": selectedThemeData.themeId,
                "cssStatus": templateFormData.customCss.cssStatus ? 1 : 0,
                "css": templateFormData.customCss.css,
                "backgroundColor": templateFormData.background.status ? templateFormData.background.color : "",
                "transparent": templateFormData.background.transparent ? 1 : 0,
                "backgroundImage": templateFormData.background.image ? templateFormData.background.image : "",
                "backgroundImageId": 0,
                "link_font": templateFormData.cardStyle.fontFamily ? templateFormData.cardStyle.fontFamily.family : "",
                "css_font": templateFormData.cardStyle.fontFamily ? templateFormData.cardStyle.fontFamily.family : "",
                "font_varient": templateFormData.cardStyle.fontVarient && Object.keys(templateFormData.cardStyle.fontVarient).length > 0 ? templateFormData.cardStyle.fontVarient.value : "",
                "fontColor": templateFormData.cardStyle.textColor,
                "cardColor": templateFormData.cardStyle.cardColor,
                "postText": 0,
                "postAuthor": templateFormData.advanced.author ? 1 : 0,
                "postTime": templateFormData.advanced.time ? 1 : 0,
                "postFeatured": templateFormData.themeSettings.postPopUp ? 1 : 0,
                "minimumPostWidth": templateFormData.themeSettings.minPostWitdh,
                "padding": templateFormData.themeSettings.postPadding,
                "scrollDelay": templateFormData.themeSettings.scrollSpeed,
                "slideDelay": templateFormData.themeSettings.timePerSlide,
                "numberOfPosts": templateFormData.themeSettings.numberOfPosts,
                "trimcontent": templateFormData.themeSettings.trimContent ? 1 : 0,
                "playFullVideo": 1,
                "randomizePost": templateFormData.themeSettings.suffleAssets ? 1 : 0,
                "hideUrls": templateFormData.themeSettings.hideUrls ? 1 : 0,
                "fitRow": templateFormData.themeSettings.bestFit ? 1 : 0,
                "roundEdge": 5,
                "cardType": 1,
                "socialAction": templateFormData.cardStyle.socialActions ? 1 : 0,
                "timeForPopup": templateFormData.themeSettings.popUpduration,
                "authorColor": templateFormData.cardStyle.authorColor,
                "iconType": templateFormData.cardStyle.iconType ? 1 : 505,
                "iconColor": templateFormData.cardStyle.socialIconColor,
                "fontSize": parseInt(templateFormData.cardStyle.fontSize),
                "shareOption": templateFormData.cardStyle.shareOptions ? 1 : 0,
                "hideContent": templateFormData.advanced.hideContent ? 1 : 0,
                "numberOfCoumn": templateFormData.themeSettings.columns && Object.keys(templateFormData.themeSettings.columns).length > 0 ? templateFormData.themeSettings.columns.value : 0,
                "slideDuration": 0,
                "slidePost": 0,
                "autoPlay": 0,
                "popupSlideShow": 0,
                "customUrl": templateFormData.themeSettings.popUpUrl && String(templateFormData.themeSettings.popUpUrl).length > 0 ? templateFormData.themeSettings.popUpUrl : "",
                "showHideOldPost": templateFormData.themeSettings.hideOldAssets.range == 8 ? templateFormData.themeSettings.hideOldAssets.showHideOldPost : 0,
                "showHideOlderPost": templateFormData.themeSettings.hideOldAssets.range ? templateFormData.themeSettings.hideOldAssets.range : 0,
                "loadMoreStatus": templateFormData.themeSettings.showmore ? 1 : 0,
                "autoScrollStatus": templateFormData.themeSettings.autoLoad ? 1 : 0,
                "hidePosts": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "onsite_settings": JSON.stringify(onsite_settings)
            }
            this.setState({ isRequesting: true, tempError: false })
            createTemplate(data).then((response) => {
                self.setState({ isRequesting: false, tempError: false })
                toast.success("Template has been successfully updated.")
                self.props.history("/template")
            }).catch((ex) => {
                toast.error("some error occurred")
            })
        }
        else {
            if (String(templateName).length == 0) this.setState({ tempError: true })
            toast.error("Please check all required fields.")
        }
    }




    render() {

        const { themeList, fonts, activeTheme, onSiteSetting } = this.props
        const { selectedTheme, selectedThemeData, templateName, templateFormData, currentTheme, isRequesting, tempError } = this.state
        return <>
            <FontHead fonts={fonts} />
            <div className="innerAside flex-shrink-0">
                <div className="card mb-4">
                    <div className="card-body p-0">
                        <div className="p-3">
                            <div className="pb-3">
                                <label className="text-gray-800 mb-2">Template Name <strong className="text-danger">*</strong></label>
                                <div className="">
                                    <input type="text" name="templateName" className={`form-control form-control-sm ${tempError ? `is-invalid` : ``}`} placeholder="Enter Template name..." onChange={this.onTextTemplateName} value={templateName} />
                                    {tempError ? <div className="invalid-feedback">Please provide template name</div> : null}
                                </div>
                            </div>
                            <div className="template_aside_inner">
                                <Accordion defaultActiveKey="1">
                                    <Accordion.Item eventKey="1" className="pb-3">
                                        <Accordion.Header className="w-100">
                                            <span className="fs-9 fw-medium">{selectedTheme && selectedTheme.name ? selectedTheme.name : `Choose Theme`}</span>
                                        </Accordion.Header>
                                        <Accordion.Body className="border rounded-1 mt-1">
                                            <ToggleButtonGroup type="radio" className="flex-column w-100" name="themeName">
                                                {themeList.map((item, key) => {
                                                    return <ToggleButton key={`theme_${item.themeId}`} id={`theme-${item.themeId}`} className={`p-0 card rounded-1 text-decoration-none cursor-pointer mb-2 ${item.themeId == selectedTheme.themeId ? `active` : ``}`}
                                                        onClick={this.onChooseTheme({ name: item.name, themeId: item.themeId }, item)} value={item.themeId}>
                                                        <div className="card-body p-2">
                                                            <div className="d-flex align-items-center">
                                                                <div className="symbol symbol-40 flex-shrink-0 me-2 text-gray-400">
                                                                    <SVG src={baseUrl(`media/images/themes/${item.themeId}.svg`)} />
                                                                </div>
                                                                <div className="overflow-hidden">
                                                                    <p className="fs-8 fw-semibold mb-1 text-gray-dark text-ellipsis text-start">{item.name}</p>
                                                                    <p className="fs-9 fw-semibold mb-0 text-gray-600 text-ellipsis text-start">{item.product_type == 1 ? `Widget` : item.product_type == 2 ? `Display` : ``}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </ToggleButton>
                                                })
                                                }
                                            </ToggleButtonGroup>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                                {
                                    selectedTheme && Object.keys(selectedTheme).length > 0 ? <>{selectedTheme.themeId == 4 ? <Suspense fallback={<></>}>
                                        <SquarePhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 21 ? <Suspense fallback={<></>}>
                                        <ModernSlideshowTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 20 ? <Suspense fallback={<></>}>
                                        <ModernCardTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 44 ? <Suspense fallback={<></>}>
                                        <AtmosphereTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 50 ? <Suspense fallback={<></>}>
                                        <GalleryPhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 47 ? <Suspense fallback={<></>}>
                                        <HorizontalColumnTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : selectedTheme.themeId == 58 ? <Suspense fallback={<></>}>
                                        <ModernWaterfallTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} onSiteSetting={onSiteSetting} />
                                    </Suspense> : null}</> : null
                                }
                            </div>
                        </div>
                        <div className="bg-white pt-2 w-100 p-3 rounded-2">
                            <button type="button" className={`btn btn-primary w-100 ${isRequesting ? `spinner disabled` : ``}`} onClick={selectedTheme ? this.onClickSaveChanges : null}>Update Changes</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex flex-column overflow-hidden w-100">
                <div className="card">
                    <div className="card-body content_wrapper">
                        <div className="tb_app_container">
                            {templateFormData && Object.keys(templateFormData).length > 0 && selectedTheme && Object.keys(selectedTheme).length > 0 ? <ThemesPreview themeName={selectedTheme.themeId} templateFormData={this.state.templateFormData} /> : null}
                        </div>
                    </div>
                </div>
            </div>
        </>
    }
}

export default memo(TemplateAside);